<template>
  <nav>
    <router-link to="/">Home</router-link>
    <router-link to="/cart">Cart ({{ cartCount }})</router-link>
    <router-link to="/wishlist">Wishlist ({{ wishCount }})</router-link>
    <router-link v-if="!user" to="/login">Login</router-link>
    <router-link v-if="!user" to="/signup">Signup</router-link>
    <button v-if="user" @click="logout">Logout</button>
  </nav>
</template>

<script setup>
import { computed } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

const store = useStore();
const router = useRouter();

const user = computed(() => store.state.user);
const cartCount = computed(() => store.state.cart.length);
const wishCount = computed(() => store.state.wishlist.length);

const logout = () => {
  store.commit('setUser', null);
  router.push('/');
};
</script>
